@import url(https://cdn.jsdelivr.net/npm/bootstrap@4.5.3/dist/css/bootstrap.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.white {
  color: white;
}

.lightgreen {
  color: #099686;
}

.red {
  color: red;
}

.green {
  color: #008000;
}

.small-para {
  font-size: 12px;
}

.small-num {
  font-size: 9px;
}

.border-green {
  border-left: 2px solid #009686;
}

a:hover {
  color: white;
}

.margin-none {
  margin: 0 !important;
}

.MuiSelect-select.MuiSelect-select {
  /* background-color: #27273d; */
  color: white;
  padding-left: 10px;
  border-radius: 10px;
  border-bottom: none;
  padding-top: 10px;
  padding-left: 10px;
}

.MuiInput-underline {
  color: transparent;
}

.MuiSelect-select.MuiSelect-select:focus {
  /* background-color: #132660; */
  color: white;
  padding-left: 10px;
  border-radius: 10px;
  border-bottom: none;
}

.MuiSelect-icon {
  color: white !important;
}

.MuiMenu-list {
  background-color: #222a31;
  color: white;
}

.MuiTablePagination-menuItem {
  /* background-color: #132660; */
  color: black;
}

.PrivateNotchedOutline-root-186,
.MuiOutlinedInput-notchedOutline {
  border-color: #19C6C7 !important;
}

.MuiInputLabel-formControl,
.MuiInputLabel-animated,
.MuiInputLabel-outlined {
  color: #19C6C7 !important;
}

.MuiInputBase-input,
.MuiOutlinedInput-input {
  height: 20px !important;
}

.MuiBottomNavigationAction-root {
  /* color: white !important;
  font-size: 30px; */
  transition: .5s linear;
}

.MuiBottomNavigationAction-root:hover {
  color: white !important;
  font-size: 30px;
  transition: .5s linear;
}

.main-btn {
  color: white;
  background-color: #DC3545;
}

.main-btn:hover {
  color: white;
  background-color: #DC3545;
}

.contact-link:hover {
  color: #1976D2;
}
.PrivateTabIndicator-colorPrimary-45{
    background-color: transparent !important;
}
.Dtt_box {
  border-radius: 100%;
}

.Account_success {
  justify-content: space-between;
}

.succse_badges {
  /* width: 10%;  */
  background-color: #00a0fe;
  vertical-align: middle;
  padding: 7px 12px;
  font-weight: 600;
  letter-spacing: 0.3px;
  border-radius: 30px;
  font-size: 12px;
}

.link_success_badges {
  background-color: #07052f;
  width: 5rem;
  vertical-align: middle;
  padding: 7px 12px;
  font-weight: 600;
  letter-spacing: 0.3px;
  border-radius: 30px;
  font-size: 12px;
}

.success-space-line {
  margin: 10px auto;
  background-color: #00ffe9;
  width: 100%;
  height: 1px;
}
.success-page-space-line {
  margin: 10px auto;
  background-color: #ffff;
  width: 100%;
  height: 1px;
}

.Dashboard_boxButton {
  -webkit-appearance: none;
  border: 0;
  outline: 0;
  position: relative;

  background: linear-gradient(
    90deg,
    rgba(57, 55, 62, 1) 26%,
    rgba(54, 73, 108, 1) 53%,
    rgba(28, 35, 47, 1) 84%
  );
  background-size: 100% 100%;
  border-radius: 100px;
  font-size: 1.3rem;
  line-height: 1.6rem;
  font-family: "Source Code Pro";
}

.question_mark {
  text-align: center;
}

.question_mark p {
  color: white;
}

.question_mark p svg {
  width: 50px;
  height: 50px;
  color: white;
}

.table_top_border {
  border-bottom: 2px solid #32adcf !important;
}

.box1 {
  border-radius: 20px;
  background-size: cover;
  background-position: bottom;
}

.login-space-line {
  margin: 10px auto;
  background-color: #08d765;
  width: 90%;
  height: 1px;
}

.login-button input {
  border: 1px solid #0062cc;
  background: transparent;
  padding: 10px 5px;
  color: rgb(138, 133, 133);
  border-radius: 20px;
  width: 60%;
}

.login-button {
  text-align: center;
  width: 100%;
  margin-top: 2%;
  margin-bottom: 2%;
}
.Wallet_Button button {
  cursor: pointer;
  background-color: #00ffe9;
  color: #fff;
  font-size: 1.15em;
  border: none;
  border-radius: 20px;
  width: auto;
  padding: 1rem 3rem;
  transition: 0.8s;
  font-weight: 600;
}
.sinIn_alreadyRe {
  color: #08d765 !important;
}

.App {
  text-align: center;
}

::selection {
  background: #08d765;
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Dtt_box {
  border-radius: 100%;
}
.box_two {
  background: linear-gradient(45deg, #08d765 10%, #08d765 90%);
}
.boxButton {
  background: linear-gradient(90deg, #132660 10%, #0062cc 90%);
  border-radius: 20px !important;
}
.table_top_border {
  border-bottom: 2px solid #08d765 !important;
}

.bottom_navi_gation .MuiBottomNavigationAction-label.Mui-selected {
  font-size: 10px !important;
}

.bottom_navi_gation .MuiBottomNavigationAction-label {
  font-size: 10px;
}

