.register-logo {
  width: 130px !important;
  border-radius: 50%;
}

.yt-video-div {
  background-color: #2e3880;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

@media screen and (max-width: 612px) {
  .set-pool-and-supply {
    width: 100% !important;
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

.stakeContainerCheck .MuiSelect-select.MuiSelect-select {
  color: #da680f !important;
}

.roatate-image {
  animation: rotation 4s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

